<template>
  <div class="bg_center">
    <br>
    <h1>在京都的两周</h1>
    <hr><hr>
    <article>
      <section>
        京都古称"平安京"，其建设效仿唐代的长安和洛阳，以朱雀大路为中心，城市分为右京（又称“长安”）和左京（又称“洛阳”）两个对称的部分。
        后来左京成为京都主要发展的地区，"洛阳"也成为京都的代称。
      </section>
    </article>

    <br><br>
    <ul>
      <h1>洛中</h1>
      <hr>
      <h2 v-for="(temple, id) in rakutyu" :key="'rakutyu'+id">
        {{temple.temple}}
        <p>{{temple.rank}}</p>
      </h2>
  
      <br>

      <h1>洛東</h1>
      <hr>
      <h2 v-for="(temple, id) in rakutou" :key="'rakutou'+id">
        {{temple.temple}}
        <p>{{temple.rank}}</p>
      </h2>
      <br>

      <h1>洛南</h1>
      <hr>
      <h2 v-for="(temple, id) in rakunan" :key="'rakunan'+id">
        {{temple.temple}}
        <p>{{temple.rank}}</p>
      </h2>
      <br>

      <h1>洛西</h1>
      <hr>
      <h2 v-for="(temple, id) in rakusai" :key="'rakusai'+id">
        {{temple.temple}}
        <p>{{temple.rank}}</p>
      </h2>
      <br>

      <h1>洛北</h1>
      <hr>
      <h2 v-for="(temple, id) in rakuho" :key="'rakuho'+id">
        {{temple.temple}}
        <p>{{temple.rank}}</p>
      </h2>
      <br>
    </ul>
    <br>
  </div>
</template>


<script>
export default {
  name: 'Kyoujiin',
  data () {
    return {
      rakutyu: [
        { id: 1, temple: '東本願寺'},
        { id: 2, temple: '西本願寺'},
        { id: 3, temple: '釘抜地蔵（石像寺）'},
        { id: 4, temple: '下鴨神社'},
        { id: 5, temple: '二条城'},
      ],

      rakutou: [
        { id: 1, temple: '銀閣寺（慈照寺）'},
        { id: 2, temple: '南禅寺', rank: '京都五山別格'},
        { id: 3, temple: '知恩院'},
        { id: 4, temple: '清水寺', rank: '京都五山第三位'},
        { id: 5, temple: '建仁寺'},
        { id: 6, temple: '東福寺', rank: '京都五山第四位'},
        { id: 7, temple: '芬陀院・雪舟寺'},
        { id: 8, temple: '泉涌寺'},
        { id: 9, temple: '平安神宮'},
      ],

      rakunan: [
        { id: 1, temple: '東寺（教王護国寺）'},
        { id: 2, temple: '醍醐寺'},
        { id: 3, temple: '平等院'},
        { id: 4, temple: '宇治上神社'},
        { id: 5, temple: '伏見稲荷大社'},
      ],

      rakusai: [
        { id: 1, temple: '金閣寺（鹿苑寺）'},
        { id: 2, temple: '龍安寺'},
        { id: 3, temple: '仁和寺'},
        { id: 4, temple: '妙心寺'},
        { id: 5, temple: '天龍寺', rank: '京都五山第一位'},
      ],

      rakuho: [
        { id: 1, temple: '鞍馬寺'},
        { id: 2, temple: '赤山禅院'},
        { id: 3, temple: '大徳寺'},
        { id: 4, temple: '瑞峯院'},
        { id: 5, temple: '修学院離宮'},
        { id: 6, temple: '貴船神社'},
      ],
    }
  }
}
</script>

<style>
</style>


