<template>
  <div class="bg_center">
    <br>
    <h1>海浪，电铁和古寺</h1>
    <hr><hr>
    <article>
      <section>
        镰仓三面环山，一面向海，曾经是镰仓幕府武家政权的所在地。区域内寺社众多，且保有着具有文化价值的中世纪建筑遗迹。
      </section>
    </article>

    <br>
    <br><br>
    <ul>
    <h2 v-for="(temple, id) in kamatemple" :key="'kamatemple'+id">
      {{temple.temple}}
      <p>{{temple.rank}}</p>
    </h2>
    </ul>
    <br>
  </div>
</template>


<script>
export default {
  name: 'Kamajiin',
  data () {
    return {
      kamatemple: [
        { id: 1, temple: '建長寺', rank: '鎌倉五山第一位'},
        { id: 2, temple: '円覚寺', rank: '鎌倉五山第二位'},
        { id: 3, temple: '寿福寺', rank: '鎌倉五山第三位'},
        { id: 4, temple: '浄智寺', rank: '鎌倉五山第四位'},
        { id: 5, temple: '浄妙寺', rank: '鎌倉五山第五位'},
        { id: 6, temple: '英勝寺'},
        { id: 7, temple: '海蔵寺'},
        { id: 8, temple: '護国寺'},
        { id: 9, temple: '浄光明寺'},
        { id: 10, temple: '極楽寺'},
        { id: 11, temple: '長勝寺'},
        { id: 12, temple: '杉本寺'},
        { id: 13, temple: '東慶寺'},
        { id: 14, temple: '明月院'},
      ],
    }
  }
}
</script>

<style>
</style>


