//使用module拆分store
const moduleA = {
  state: {
    msg: 'Welcome to Your Vue.js AppX',
    // cartArry: JSON.parse(localStorage.getItem('memo')) || [],
  },

  getters: {
    uppercase (state) {
      state.msg = state.msg.toUpperCase();
      return state.msg
    }
  },
}

const moduleB = {
  state: {
    count: 1,
  },

  mutations: {
    increment (state) {
      state.count++
    }
  },

  // Action 类似于 mutation，不同在于：
  // Action 提交的是 mutation，而不是直接变更状态。
  // Action 可以包含任意异步操作。
  actions: {
    increment (context) {
      context.commit('increment') //包含了使用mutation时的commit命令
    }
  },
}

const store = new Vuex.Store({
  modules: {
    a: moduleA, // 
    b: moduleB, // 
  }
})

// store.state.a // -> moduleA 的状态
// store.state.b // -> moduleB 的状态

// store.subscribe((mutations, state)=>{
//   localStorage.setItem('memo', JSON.stringify(state.memo)) })


export default store;





